.bg-warning {
    --bs-bg-opacity: .33;
    background-color: rgba(var(--bs-danger-rgb), var(--bs-bg-opacity)) !important;
}
.bg-danger, .forgot-clock-out .danger {
    --bs-bg-opacity: .7;
    background-color: rgba(var(--bs-danger-rgb), var(--bs-bg-opacity)) !important;
}
.minimal {
    --bs-bg-opacity: .33;
    background-color: rgba(var(--bs-success-rgb), var(--bs-bg-opacity)) !important;
}
.medium {
    background-color: rgba(245, 180, 39, 0.5) !important;
}
.high {
    --bs-bg-opacity: .33;
    background-color: rgba(var(--bs-danger-rgb), var(--bs-bg-opacity)) !important;
}
.danger {
    --bs-bg-opacity: .7;
    background-color: rgba(var(--bs-danger-rgb), var(--bs-bg-opacity)) !important;
}

@media print {
    #upload, #upload, #navbar {
        display: none;
    }
    button {
        display: none;
    }
    .container {
      min-width: 100vw !important;
      max-width: 100vw !important;
    }
    body, .container, .row, .table, .table th, .table td {
      margin: 0 !important;
      padding: 1px !important;
      font-size: 10px; /* Smaller font size */
    }
  
    .container {
      width: 100% !important;
      padding-left: 0 !important;
      padding-right: 0 !important;
    }
  
    .row {
      margin-left: 0 !important;
      margin-right: 0 !important;
    }
  
    .table {
      width: 100%;
      border-collapse: collapse;
    }
  
    .uniform-height {
      height: 30px;
    }
  
    .chart-container {
      width: 33%;
      margin-left: 0 !important;
      padding-left: 0 !important;
      margin-bottom: 10px;
      padding-bottom: 0;
    }
  
    .chart-inner {
      width: 100%;
      height: auto;
      margin: 0 auto;
    }
  
    .chart-inner.pie-chart {
      width: 150px; /* Adjust as needed */
      height: 150px;
    }
  
    .chart-inner.pie-chart canvas,
    .chart-inner canvas {
      width: 100% !important;
      height: auto !important;
    }
  
    h4 {
      font-size: 12px; /* Adjust as needed */
      margin-bottom: 5px;
    }
  }
  @media print {
    .page-break {
      page-break-before: always;
      break-before: page;
    }
  }